@font-face {
  font-family: gintronic;
  src: url("../fonts/c59ee803ebdeddbc5a7962648bcaa5e6.ttf") format("truetype");
}
@tailwind base;
@tailwind components;
@tailwind utilities;
@media (max-width: 640px) {
  button,
  a,
  p,
  span {
    font-size: 10px;
  }
}
body {
  background-image: url("../images/wallpaperflare.com_wallpaper.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  font-family: gintronic;
  font-weight: 700;
}
body a {
  text-decoration: none;
}

nav {
  position: fixed;
  top: 0;
  z-index: 1000;
}
nav a, nav button {
  color: white;
}
nav button {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
}
nav .active {
  font-weight: bold;
  border-bottom: 2px #00adb9 solid;
  animation: opacity 0.4s;
}
@keyframes opacity {
  from {
    transform: scale(0.9);
  }
  to {
    transform: scale(1);
  }
}

.home .top h1 {
  font-size: 100px;
  font-weight: bolder;
  color: white;
}
.home .top .image-container {
  display: inline-block;
  position: relative;
}
.home .top .image-container img {
  z-index: 2;
  display: block;
  width: 100%;
  height: auto;
  border-radius: 50%;
  /* Use 50% for a circular shape, adjust as needed */
  -webkit-clip-path: circle(50% at center);
          clip-path: circle(50% at center);
  /* Clip the image to a circle */
  animation: color 10s infinite alternate;
}
@keyframes color {
  from {
    filter: drop-shadow(5px 10px 20px #ff6e6e);
  }
  to {
    filter: drop-shadow(5px 10px 20px #6f24f8);
  }
}
.home .top .image-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.1);
  /* Shadow color with transparency */
  border-radius: 50%;
  /* Ensure the shadow is also circular */
  z-index: -1;
  /* Behind the image */
}
.home .top .image-container .circle {
  filter: drop-shadow(5px 1px 10px #ffffff);
  z-index: -1;
  width: 20%;
  height: 20%;
  background: radial-gradient(circle at 100px 300px, #ffffff, #ffffff);
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  margin: auto;
}
.home .top .icons {
  position: absolute;
  right: 0;
  top: 50%;
}
.home .top .icons .icon, .home .top .icons .icon-text {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  color: #00022B !important;
  padding: 5px;
  border-radius: 3px;
}
.home .middel .col {
  min-height: 30vh;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  border-radius: 5px;
}

.random-icons {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 300vh;
}
.random-icons .row {
  filter: drop-shadow(5px 1px 10px #ffffff);
  z-index: 100;
  width: 50px;
  height: 50px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  cursor: grab;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}/*# sourceMappingURL=main.css.map */